.pt-key {
  display: inline-block;
  border-radius: 2px;
  box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.1), 0 1px 1px rgba(16, 22, 26, 0.2), 0 2px 6px rgba(16, 22, 26, 0.2);
  background: #ffffff;
  min-width: 25px;
  height: 25px;
  padding: 3px 5px;
  vertical-align: middle;
  text-align: center;
  line-height: 20px;
  color: #5c7080;
  font-family: -apple-system, "BlinkMacSystemFont", "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Open Sans", "Helvetica Neue", "Icons16", sans-serif;
  font-size: 12px; }
  .pt-key.pt-modifier-key {
    padding: 3px 8px 3px 6px; }
    .pt-key.pt-modifier-key .pt-icon-standard {
      margin-right: 5px; }
  .pt-dark .pt-key {
    box-shadow: 0 0 0 1px rgba(16, 22, 26, 0.2), 0 1px 1px rgba(16, 22, 26, 0.4), 0 2px 6px rgba(16, 22, 26, 0.4), inset 0 1px 0 rgba(255, 255, 255, 0.05);
    background: #394b59;
    color: #bfccd6; }

.pt-key-combo .pt-key:not(:last-child) {
  margin-right: 5px; }

.pt-hotkey-dialog {
  top: 40px;
  padding-bottom: 0; }
  .pt-hotkey-dialog .pt-dialog-body {
    margin: 0;
    padding: 0; }
  .pt-hotkey-dialog .pt-key-combo {
    flex: 1 0 auto;
    width: 160px;
    text-align: right; }

.pt-hotkey-column {
  margin: auto;
  max-height: 80vh;
  overflow-y: auto;
  padding: 30px; }
  .pt-hotkey-column .pt-hotkey-group {
    margin-bottom: 30px; }
    .pt-hotkey-column .pt-hotkey-group:not(:first-child) {
      margin-top: 50px; }

.pt-hotkey {
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  margin-right: 0;
  margin-left: 0; }
  .pt-hotkey:not(:last-child) {
    margin-bottom: 10px; }

