.pt-large - Large tabs

Styleguide pt-tabs
*/
.pt-tabs.pt-vertical {
  display: flex; }
  .pt-tabs.pt-vertical > .pt-tab-list {
    flex-direction: column;
    align-items: flex-start; }
    .pt-tabs.pt-vertical > .pt-tab-list .pt-tab {
      width: 100%;
      padding: 0 10px; }
    .pt-tabs.pt-vertical > .pt-tab-list .pt-tab-indicator-wrapper .pt-tab-indicator {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      border-radius: 3px;
      background-color: rgba(19, 124, 189, 0.2);
      height: auto; }
  .pt-tabs.pt-vertical > .pt-tab-panel {
    margin-top: 0;
    padding-left: 20px; }

.pt-tab-list {
  display: flex;
  flex: 0 0 auto;
  align-items: flex-end;
  position: relative;
  margin: 0;
  border: none;
  padding: 0;
  list-style: none; }
  .pt-tab-list > *:not(:last-child) {
    margin-right: 20px; }

.pt-tab {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal;
  flex: 0 0 auto;
  position: relative;
  cursor: pointer;
  max-width: 100%;
  vertical-align: top;
  line-height: 30px;
  color: #182026;
  font-size: 14px; }
  .pt-tab-indicator-wrapper ~ .pt-tab {
    box-shadow: none !important; }
  .pt-tab[aria-disabled="true"] {
    cursor: not-allowed;
    color: rgba(92, 112, 128, 0.5); }
  .pt-tab[aria-selected="true"] {
    border-radius: 0;
    box-shadow: inset 0 -3px 0 #106ba3; }
  .pt-tab[aria-selected="true"], .pt-tab:not([aria-disabled="true"]):hover {
    color: #106ba3; }
  .pt-tab:focus {
