      border-radius: 15px; }
    .pt-tag.pt-large.pt-tag-removable,
    .pt-large .pt-tag.pt-tag-removable {
      padding-right: 30px; }
  .pt-tag.pt-intent-primary {
    background: #137cbd;
    color: #ffffff; }
  .pt-tag.pt-intent-success {
    background: #0f9960;
    color: #ffffff; }
  .pt-tag.pt-intent-warning {
    background: #d9822b;
    color: #ffffff; }
  .pt-tag.pt-intent-danger {
    background: #db3737;
    color: #ffffff; }
